import React, { useState } from "react";
import { Form, Button, InputGroup } from "react-bootstrap";
import { connect } from "react-redux";
import { login } from "../actions/auth";
import { Redirect } from "react-router-dom";
import { bindActionCreators } from "redux";

function Login(props) {
  const [validated, setValidated] = useState(false);
  const [login, setLogin] = useState({
    username: "",
    password: "",
  });
  document.title = "Fashion Store | Login";
  const handleSubmit = (event) => {
    const form = event.currentTarget;
    event.preventDefault();
    if (form.checkValidity() === false) {
      event.stopPropagation();
    }
    setValidated(true);
    props.login(login.username, login.password);
  };
  const onChange = (e) =>
    setLogin({
      ...login,
      [e.target.name]: e.target.value,
    });

  if (props.isAuthenticated) {
    return <Redirect to="/" />;
  }
  const { username, password } = login;

  return (
    <div className="login">
      <div
        style={{
          backdropFilter: "blur(13px)",
          backgroundColor: "#00000086",
          position: "absolute",
          height: "100%",
          width: "100%",
        }}
      />
      <Form
        noValidate
        validated={validated}
        className="formaction"
        onSubmit={handleSubmit}
      >
        <center>
          <h3>Login</h3>
        </center>
        <Form.Group controlId="validationCustomUsername">
          <InputGroup>
            <InputGroup.Prepend>
              <InputGroup.Text id="inputGroupPrepend">@</InputGroup.Text>
            </InputGroup.Prepend>
            <Form.Control
              type="text"
              placeholder="Username"
              name="username"
              value={username}
              aria-describedby="inputGroupPrepend"
              required
              onChange={onChange}
            />
            <Form.Control.Feedback type="invalid">
              Please choose a username.
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>

        <Form.Group controlId="validationCustomPassword">
          <InputGroup>
            <InputGroup.Prepend>
              <InputGroup.Text id="inputGroupPrepend">
                <i className="fa fa-lock" aria-hidden="true"></i>
              </InputGroup.Text>
            </InputGroup.Prepend>
            <Form.Control
              required
              name="password"
              type="password"
              value={password}
              placeholder="Password"
              onChange={onChange}
            />{" "}
            <Form.Control.Feedback type="invalid">
              Please enter password
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>
        <small className="text-danger ml-5">
          {props.errors.non_field_errors}
        </small>
        <center>
          <Button variant="primary" type="submit">
            Login
          </Button>
        </center>
      </Form>
    </div>
  );
}
const mapStateToProps = (state) => ({
  isAuthenticated: state.authReducer.isAuthenticated,
  errors: state.errorReducer.msg,
});
function mapDispatchToProps(dispatch) {
  return bindActionCreators({ login }, dispatch);
}
export default connect(mapStateToProps, mapDispatchToProps)(Login);
