import React, { useState } from "react";
import { Form, Button, InputGroup } from "react-bootstrap";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { signup } from "../actions/auth";
import { Redirect } from "react-router-dom";
function Signup(props) {
  const [validated, setValidated] = useState(false);
  const [register, SetRegister] = useState({
    username: "",
    password: "",
    email: "",
  });

  document.title = "Fashion Store | Signup";
  const handleSubmit = (event) => {
    event.preventDefault();
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.stopPropagation();
    }
    setValidated(true);
    props.signup(register.username, register.password, register.email);
  };
  const onChange = (e) =>
    SetRegister({
      ...register,
      [e.target.name]: e.target.value,
    });
  if (props.isAuthenticated) {
    return <Redirect to="/" />;
  }

  return (
    <div className="login">
      <div
        style={{
          backdropFilter: "blur(13px)",
          backgroundColor: "#00000086",
          position: "absolute",
          height: "100%",
          width: "100%",
        }}
      />
      <Form
        noValidate
        validated={validated}
        className="formaction"
        onSubmit={handleSubmit}
      >
        <center>
          <h3>Login</h3>
        </center>
        <Form.Group controlId="validationCustomUsername">
          <InputGroup>
            <InputGroup.Prepend>
              <InputGroup.Text id="inputGroupPrepend">@</InputGroup.Text>
            </InputGroup.Prepend>
            <Form.Control
              type="text"
              placeholder="Username"
              name="username"
              value={register.username}
              aria-describedby="inputGroupPrepend"
              onChange={onChange}
              required
            />
            <Form.Control.Feedback type="invalid">
              Please enter a username.
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>
        <Form.Group controlId="validationCustomEmail">
          <InputGroup>
            <InputGroup.Prepend>
              <InputGroup.Text id="inputGroupPrepend">
                <i className="fa fa-envelope" aria-hidden="true"></i>
              </InputGroup.Text>
            </InputGroup.Prepend>
            <Form.Control
              type="email"
              placeholder="Email"
              name="email"
              value={register.email}
              onChange={onChange}
              aria-describedby="inputGroupPrepend"
              required
            />
            <Form.Control.Feedback type="invalid">
              Enter a valid email.
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>
        <Form.Group controlId="validationCustomPassword">
          <InputGroup>
            <InputGroup.Prepend>
              <InputGroup.Text id="inputGroupPrepend">
                <i className="fa fa-lock" aria-hidden="true"></i>
              </InputGroup.Text>
            </InputGroup.Prepend>
            <Form.Control
              required
              name="password"
              value={register.password}
              type="password"
              placeholder="Password"
              onChange={onChange}
            />{" "}
            <Form.Control.Feedback type="invalid">
              Please enter password
            </Form.Control.Feedback>
          </InputGroup>
        </Form.Group>
        <center>
          <Button variant="primary" type="submit">
            Register
          </Button>
        </center>
      </Form>
    </div>
  );
}
const dispatchToProps = (dispatch) => bindActionCreators({ signup }, dispatch);
const mapStateToProps = (state) => ({
  isAuthenticated: state.authReducer.isAuthenticated,
});
export default connect(mapStateToProps, dispatchToProps)(Signup);
