import { GET_FASHIONS } from "./types";
import axios from "axios";
import { returnErrors } from "./errors";

export const getFashions = () => (dispatch) => {
  axios
    .get("/api/fashions", {
      timeout: 2000,
      timeoutErrorMessage: "Request Time out",
    })
    .then((res) => {
      dispatch({ type: GET_FASHIONS, payload: res.data });
    })
    .catch((err) => {
      dispatch(returnErrors(err.response.data, err.response.status));
    });
};
