import React from "react";
import { Carousel } from "react-bootstrap";

function DetailedView({ history, location }) {
  const { state } = location;
  
  const image = Object.values(state).filter((t, i, arr) => {
    if (!String(t).match(".jpg")) {
      return null;
    } else return t;
  });
  document.title = state.title;
  const CarouselImages = image.map((t, i, arr) => {
    return (
      <Carousel.Item key={i}>
        <img src={t} alt="asc" />
      </Carousel.Item>
    );
  });
  return (
    <div className="detailed">
      <Carousel>{CarouselImages}</Carousel>

      <h3>{state.title}</h3>

      <p>{state.description}</p>
    </div>
  );
}
export default DetailedView;
