import React, { useEffect, Fragment } from "react";
import { Switch, Route, useHistory, useLocation } from "react-router-dom";
import { getFashions } from "../actions/fashions";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import "./Styles/navigations.scss";
import { Nav, Navbar, Button } from "react-bootstrap";
import Home from "./Home";
import DetailedView from "./Detailed";
import Login from "./Login";
import Signup from "./Signup";
import PrivateRoute from "./PrivateRoute";
import { loadUser, logout } from "../actions/auth";
import Store from "../Store";
function Navigation(props) {
  const { getFashions, fashions, auth } = props;
  const history = useHistory();
  const location = useLocation();
  useEffect(() => {
    getFashions();
    return () => null;
  }, [getFashions]);
  useEffect(() => {
    Store.dispatch(loadUser());
  }, []);

  const fashionList = props.fashions.map((data, index, array) => {
    return { image: data.image_main, title: data.title, id: data.id };
  });
  const guestSection = location.pathname.match("/login") ? (
    <Button onClick={() => history.push("/signup")}>Register</Button>
  ) : (
    <Button onClick={() => history.push("/login")}>Login</Button>
  );
  const userSection = auth.user ? (
    <>
      <span className="nav-text mr-3 mt-1  text-light ">{`Welcome ${auth.user.username}`}</span>
      <Button
        size="sm"
        onClick={() => {
          props.logout();
          history.replace("/");
        }}
      >
        Logout
      </Button>
    </>
  ) : (
    ""
  );
  return (
    <Fragment>
      <Navbar fixed="top" className="navbar" variant="dark">
        <Navbar.Brand className="brand">Fashion Store</Navbar.Brand>
        <Nav>{auth.isAuthenticated ? userSection : guestSection}</Nav>
      </Navbar>
      <Fragment>
        <Switch>
          <Route path="/signup" component={Signup} />
          <Route path="/login" component={Login} />
          <Route path="/detailed/:id" component={DetailedView} />
          <PrivateRoute
            path="/"
            render={(props) => {
              return (
                <Home
                  {...props}
                  fashions={fashionList}
                  goDetail={(props) => {
                    history.push(`/detailed/${props}`, fashions[props - 1]);
                  }}
                />
              );
            }}
          />
        </Switch>
      </Fragment>
    </Fragment>
  );
}
const mapSateToProps = (state) => {
  return {
    fashions: state.fashionsReducer.fashions,
    auth: state.authReducer,
  };
};
function mapDispatchToProps(dispatch) {
  return bindActionCreators({ getFashions, logout }, dispatch);
}

export default connect(mapSateToProps, mapDispatchToProps)(Navigation);
