import React from "react";
import { ListGroup, Button } from "react-bootstrap";

function Home({ history, location, fashions, goDetail }) {
  document.title = "Fashion Store | Dashboard";
  const fashionList = fashions.map((t) => {
    return (
      <div key={t.id}>
        <span>
          <img src={t.image} alt="spider-lady" />
          <p>{t.title}</p>
          <Button
            onClick={() => {
              goDetail(t.id);
            }}
            variant="dark"
          >
            Find out
          </Button>
        </span>
      </div>
    );
  });
  return (
    <>
      <div className="home">
        <ListGroup>{fashionList}</ListGroup>
      </div>
    </>
  );
}

export default Home;
